/* eslint-disable react/prop-types */
import React from 'react';

import Box from '@mui/material/Box';
import Grid from '@mui/material/Grid';

import NInput from '../inputs/numInputs/NNumInput';
import SDInput from '../inputs/numInputs/SDNumInput';

function WelchsDfForm(props) {
  const { hookValues, hookSetters } = props;

  const {
    n1, s1, n2, s2,
  } = hookValues;

  const {
    setN1, setS1, setN2, setS2,
  } = hookSetters;

  return (
    <Box
      component="form"
      sx={{
        width: 'auto',
        '& .MuiTextField-root': { m: 1, width: '25ch' },
      }}
      noValidate
      autoComplete="off"
    >
      <Grid
        container
        alignItems="center"
        justifyContent="center"
      >

        <Grid item align="center" xs={12}>
          <NInput label="Sample size 1" hookValue={n1} hookSetter={setN1} />
          <SDInput label="Std Dev 1" hookValue={s1} hookSetter={setS1} />

          <NInput label="Sample size 1" hookValue={n2} hookSetter={setN2} />
          <SDInput label="Std Dev 1" hookValue={s2} hookSetter={setS2} />
        </Grid>

      </Grid>
    </Box>
  );
}

export default WelchsDfForm;
