import React, { useState } from 'react';
import WelchsDfForm from '../../components/forms/WelchsDfForm';
import welchsDegreesOfFreedom from '../../statistics/tTable/welchsDf';

function WelchsDf() {
  const [n1, setN1] = useState(10);
  const [s1, setS1] = useState(2);

  const [n2, setN2] = useState(10);
  const [s2, setS2] = useState(2);

  const hookValues = {
    n1, s1, n2, s2,
  };

  const hookSetters = {
    setN1, setS1, setN2, setS2,
  };

  return (
    <div className="page">
      <WelchsDfForm hookValues={hookValues} hookSetters={hookSetters} />
      <h2>{`df=${welchsDegreesOfFreedom(n1, s1, n2, s2)}`}</h2>
    </div>
  );
}

export default WelchsDf;
